import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import MediaQuery from 'react-responsive';
import SwipeableViews from 'react-swipeable-views';
import Grid from 'material-ui/Grid';
import Meta from '../Meta/Meta';
import StreamCard from '../StreamCard/StreamCard';
import Controls from '../Controls/Controls';


const styles = theme => ({
    root: {
        flexGrow: 1,
        paddingTop: 20
    },
    controls: {
        paddingTop:30,
        textAlign:'center'
    },
    meta: {
        textAlign: 'center',
        display: 'table-cell',
        verticalAlign: 'middle'
    }
});

const Container = (props) => {
    const { classes, streams, activeStream } = props;
    const active = activeStream['id'] ? true : false;


    const renderStreamCards = () => {
        return streams.map( stream => {
            return(
                <StreamCard 
                    key={stream.id} 
                    activeId={activeStream.id}
                    {...stream} />
            )
        })
    }


    return (
        <div className={classes.root}>
        {/*meta data*/}
        <Grid container justify='center' spacing={16}>
            <Grid item className={classes.meta}>
                <Meta />
            </Grid>
        </Grid>

        <Grid container spacing={16}>
            {/*tablet view*/}
            <MediaQuery query="(min-width: 1224px)">
                {renderStreamCards()}
            </MediaQuery>

            {/*desktop view*/}
            <MediaQuery query="(max-width: 1224px)">
                <SwipeableViews>
                    {renderStreamCards()}
                </SwipeableViews>
            </MediaQuery>
            
        </Grid>
        <Grid className={classes.controls} container spacing={16}>
            <Controls active={active} />
        </Grid>
        </div>
    );
}

Container.propTypes = {
    classes:PropTypes.object.isRequired
};

export default withStyles(styles, { withTheme: true })(Container);


