import React, { Component } from 'react';
import axios from 'axios';
import conf from '../../config';

const style = {
    root: {
        display: 'inline-block',
        marginRight: 15
    },
    img: {
        borderRadius: '50%'
    }
}


class MetaImg extends Component {
    constructor (props) {
        super(props)
        
        this.state = {
            img: null
        }
    }

    componentDidMount(){
        axios
            .get(`http://ws.audioscrobbler.com/2.0/?method=artist.getinfo&artist=${this.props.artist}&api_key=${conf.lastFM_API}&format=json`)
            .then(res => this.setState({ img: res.data.artist.image[1]['#text'] }))
            .catch(e => console.log(e))
    }

    render () {
        console.log(style)
        return (
            <div>
                <img style={style.img} src={this.state.img} alt="" />
            </div>
        )
    }
}
  
export default MetaImg;



